<template>
	<view class="notice">
		<image src="http://qp9mr1fhs.hn-bkt.clouddn.com/img/my-bg.jpg" class="bg"></image>
		<view class="content" :style="{height:bgHeight}">
			<view class="utitlebox" style="margin-left: 40rpx;">
			    <view class="utitleblock"></view>
			    <view class="utitle">商家须知</view>
				<view class="ts">(规则可上下滑动)</view>
			</view>
			<scroll-view class="text" scroll-y="true">
					<p>生活中处处存在着美。家里面井然有序，窗明几净，各种家什摆放错落有致，这是一种整洁的美；端庄秀丽，静谧可人，这是一种沉静的美；落落大方，清新自然，这是一种自信的美；平和洒脱，超然物外，这是一种闲适的美；粗犷豪放，不拘小节，这是一种大气的美。</p>
				
				　　<p>“清水出芙蓉，天然去雕饰”，天地自然之灵气铸就成了一种浑然天成的美，美得清秀而丰盈，是集自然之大成的一种超脱的境界。“小荷才露尖尖角”般的灵秀，使人摆脱俗气，过目难忘。这些叫人忘俗的天然之美，可能谁都会见过，只是大多忘记了欣赏，没有真正感到那种透视的美。</p>
				
				　　<p>美的感觉存在于心中，很多时候无法用文字表述出来，很多美好的思绪在脑海中一闪即过，无法捕捉。美不是空谈，而是要去体验、去感受、去欣赏。</p>
				
				　　<p>倘若说欣赏自然之美需要睿智和一双善于发现真谛的眼睛，那么欣赏人间真情，则需要有细腻的情感。在高速发达的现代社会，大多因生计而疲于奔波，身边的零散琐碎的事情往往被忽略了，渐渐地把日子过得淡然无味，一头雾水，不知道生活到底为了什么？“母亲啊，你是荷叶，我是红莲，心中的雨点来了，除了你，谁是我在无遮拦的天空下荫蔽？”，作家冰心的细腻由此可见一斑，这应该是她最真挚情感的表白。细腻的情感燃烧时，身边的细微的美肯定会熠熠生彩、璀璨夺目的。此时的生活还会索然无味么。</p>
				
				　　<p>常常感动于亲情的温暖，感动于朋友间情谊的真挚，可当有人溺水时，岸上的人或许大声呼救，或许焦急万分，但无论如何最感人的还是纵身跳下去救人的那个人，这是一种真诚无畏的美。人在落魄时，萎靡不振，孤独无助，那个能助你排忧解难的人，恰似明月的青辉毫不吝啬地倾洒入你的心田，登时那种诚挚而热心的美会感动你一生。具有先天缺陷的人，常常感动生活的苦涩，上苍的不公正，那个能助你抚慰心灵创伤、改变你命运的人总是叫人敬佩，这种无私而拯救心灵的美会让你永志不忘。</p>
				
				　　<p>身边的琐碎事情看起来凌乱而繁杂，不经意中大多放弃了，长时间的漠然必然麻木不仁，也就无从谈起美的存在。</p>
				
				　　<p>欣赏美其实很简单，如果你对内在世界的美丽漠不关心，那你无论如何也看不见外在世界的美丽。摈弃掉偏见和固执，一种前所未有的美就呈现在你眼前了，因为美就在你的心中。找到心中的美，生活中处处都能找到美。</p>
				
				　　<p>美的极致便是安详，美是一种毫无标的的愉悦。人如果能抛弃偏执，丢下无谓的烦忧，哪怕一片树叶，一朵小花，都能发现它的美，只要用心，生活中的美和喜悦便会不请自来。</p>
				
				　　<p>生活不都是快乐和幸福，同样生活也不可能全是落寞和寂寥。用一种欣赏美的眼光去看看阳光和雨露，恬淡而愉悦，用一种欣赏美的眼光去看看花草树木，清新而爽快，用一种欣赏美的眼光去看看大海，寥廓而深远……</p>
		
				　　<p>生活中的美充斥在各个角落，要的是你学会发现，学会欣赏。练就一种修养，一种品位去适时捕捉和欣赏生活中的美，为心灵开一扇窗，让智慧的光芒和生活中眩目多彩的美呈现在你眼前。</p>
				
				　　<p>经典美文摘抄2000字：凉风有信，荷香清逸</p>
				
				　　<p>时光，是可以有香气的，总觉得夏的风中，藏着一首诗，采花入韵，以叶为境，读来，平仄有声，馨香满怀。</p>
				
				　　<p>这个季节，荷花早已开满小池。晨光里，一个人于清风中去看荷，不用带许多，只一颗静心便可。远远看去，满池碧叶，近看，波光荡漾的湖面，映出荷婉约的素颜，如亭亭玉立的少女，清幽的风雅，随风流动，谁能说这不是一幅最美的画卷？我是爱荷的，爱它的清丽多姿，素雅洁净，更是爱她内心的那份淡定，风来雨去，缘来缘往，我自安然。</p>
				
				　　<p>时至八月，仍然燥热，偶尔有一场雨，便显得弥足珍贵，雨后，一切都静了下来，就如我的心，没有太多的波澜。凉风有信，荷香清逸，这样的时刻，于夏日该是良辰。以素白为线，绣一窗琉璃，途经花树与阳光，如若简单，便能书一地诗心。</p>
				
				　　<p>清凉的光阴，让人想起那首禅诗，春有百花秋有月，夏有凉风冬有雪，若无闲事挂心头，便是人间好时节。世间的好千般万般，没有太多执念的安静，成就了内心的喜欢，相对天荒地老，我选择岁月无恙。时光赠予我的，是深深的懂得。</p>
				
				　　<p>总是希望，光阴能慢下来，日子能简而又简，若是可以，能让我幽居俗世红尘，养花，种菜，读散淡清逸的句子，看墨色游走宣纸，种一篱修竹，在树荫下悠然，看潮听风，手中的书似有似无的翻着，闻一闻栀子花开的香气，体味一下兰花的幽静，让心在阳光下舒展一些，轻盈一些，拈一瓣落花的芬芳，取一片葱茏的碧绿，于草木闲情中，养一抹心香来供养灵魂，这样的时光，便有了清凉的美意。</p>
				
				　　<p>一直喜欢禅意且诗情的句子，如白马入芦花，银碗里盛雪，寂寂小语，如清风月白，落在心上。浮夏，酷热难耐，幸好有拈笔落墨的绿意和清凉，将心事安宁在其中，便可享受温婉的时光。闲来读《诗经》里的那句，蒹葭苍苍，白露为霜，所谓伊人，在水一方，眼前便会浮现美丽且羞涩的女子，在清晨朦胧的薄雾里走来，清凉气息忽远忽近。</p>
				
				　　<p>总觉得这样的句子是带着清露的，给人以美好，让人有清水涤心的澄澈。养一畦露水，在露水中养一个清凉的灵魂，这样不论在何时，都不会觉得浮燥，生命，是短暂而渺小的，愿求无尘无染，无论何时回望，都能遇到一个澄澈晶莹的自己。</p>
				
				　　<p>很想像一株植物那样安静的生长，只闻花香，不谈悲喜，可是在这繁华的尘世间，是很难做到如此淡定的，所有安静的抵达，从来都要经过千山万水，能把流水落花春也去，看成天上人间的，定是有禅定和佛性的人，因为经历，所以懂得，因为懂得，所以慈悲，如此，出淤泥而不染的莲花要修行多少年，方可做到闲花自开，纯静清雅。</p>
				
				　　<p>修行，是要有一定过程的，若能修得一颗体贴万物的心，便能温柔的对待一草一木，清风明月，静水深流；修得一颗明亮的心，便可听夏虫呢喃，可草木素心，亦可与美文清乐，眼里皆是万物的洁净与美好</p>
				
				　　<p>有些美，洁净素雅，不蔓不枝，却能美在心上的，夏日能入我心的，不是极尽妖娆的蔷薇，也不是开的花枝乱颤的牡丹，而是素素开在光影中的莲。清晨薄风中的莲，旁逸洁出，暗香盈袖，透着淡泊和静好，不必过多展示自已，那一低头的羞涩，一举手的婀娜，如一阕清词，让人一读再读，流连忘返。</p>
			</scroll-view>

